import { Component, EventEmitter, Input, Output } from '@angular/core';
import { BaseModel, Lookup } from '../../shared';

export interface DataGridPagination {
  currentPage: number;
  pageSizes: Lookup
  pageSize: number;
  pageCount?: number;
  pages?: number[];
}

export interface DatagridColumn {
  label: string
  path: string
  formatFn?: (value: any) => string
}

export interface DatagridOptions {
  columns: DatagridColumn[]
  showAuditTimestamps?: boolean
  canEdit?: boolean
  canDelete?: boolean
}

@Component({
  selector: 'quick-datagrid',
  templateUrl: './datagrid.component.html',
  styleUrl: './datagrid.component.scss'
})
export class DatagridComponent {
  @Input() data: Partial<BaseModel>[] = []
  @Input() pagination?: DataGridPagination
  @Input() options?: DatagridOptions

  @Output() onLoadRequested = new EventEmitter<DataGridPagination | void>();
  @Output() onEditRequested = new EventEmitter<number>();
  @Output() onDeleteRequested = new EventEmitter<number>();

  getProperty(obj: any, column: DatagridColumn): string {
    const keys = column.path.split(".");

    let value = "";
    keys.forEach(key => value = (!value ? obj : value)[key]);

    if (column.formatFn)
      return column.formatFn(value);

    return value;
  }

  edit(item: Partial<BaseModel>) {
    this.onEditRequested.emit(item.id);
  }

  askForDelete(item: Partial<BaseModel>) {
    if (!confirm("Confirma a exclusão do registro selecionado? essa operação não pode ser desfeita!"))
      return

    this.onDeleteRequested.emit(item.id)
  }

  nextPage() {
    if (!this.pagination)
      return

    this.pagination.currentPage++
    this.onLoadRequested.emit(this.pagination)
  }

  changePage(pageNumber: number) {
    if (!this.pagination)
      return

    this.pagination.currentPage = pageNumber
    this.onLoadRequested.emit(this.pagination)
  }

  previousPage() {
    if (!this.pagination)
      return

    this.pagination.currentPage--
    this.onLoadRequested.emit(this.pagination)
  }

  changePageSize($event: string | number) {
    if (!this.pagination)
      return

    this.pagination.pageSize = <number>$event
    this.onLoadRequested.emit(this.pagination)
  }
}
