import { HttpClient, HttpParams } from "@angular/common/http";
import { Observable } from "rxjs";
import { BaseModel, Lookup, Page } from "../models";
import { FilterBuilder, FilterLogicalGroup } from "../../../public-api";

export abstract class RestService<T extends BaseModel> {
    protected abstract api: string;

    constructor(
        protected http: HttpClient
    ) { }

    private getQuery(filter?: FilterLogicalGroup) {
        let params = new HttpParams();

        if (filter) {
            const builder = new FilterBuilder(filter)
            params = params.append("filter", builder.encode())
        }

        return params;
    }

    query(filter?: FilterLogicalGroup): Observable<T[]> {
        let params = this.getQuery(filter);
        return this.http.get<T[]>(this.api, { params });
    }

    paginate(page: number, pageSize?: number, filter?: FilterLogicalGroup): Observable<Page<T>> {
        let params = this.getQuery(filter);

        params = params.append("page", page);
        if (pageSize)
            params = params.append("pagesize", pageSize);

        return this.http.get<Page<T>>(this.api, { params });
    }

    lookup(filter?: FilterLogicalGroup): Observable<Lookup> {
        let params = this.getQuery(filter)
        return this.http.get<Lookup>(`${this.api}/lookup`, { params });
    }

    getById(id: number): Observable<T> {
        return this.http.get<T>(`${this.api}/${id}`);
    }

    insert(dados: Partial<T>): Observable<T> {
        return this.http.post<T>(`${this.api}`, dados);
    }

    update(id: number, dados: Partial<T>): Observable<T> {
        return this.http.post<T>(`${this.api}/${id}`, dados);
    }

    delete(id: number): Observable<any> {
        return this.http.delete<any>(`${this.api}/${id}`);
    }

    bulk(dados: Partial<T>[]) {
        return this.http.post<T[]>(`${this.api}/bulk`, dados);
    }
}