import { Component, EventEmitter, Input, Output } from '@angular/core';
import { BaseComponent } from '../../ui/helpers/base/base.component';

export type PickerOption = {
  value: any,
  displayText: string
}

export type PickerOptions = PickerOption[]

@Component({
  selector: 'quick-picker',
  templateUrl: './picker.component.html',
  styleUrls: [
    '../../ui/helpers/base/base.component.scss',
    './picker.component.scss'
  ]
})
export class PickerComponent extends BaseComponent {
  @Input() placeholder: string = "Selecione"
  @Input() options: PickerOptions = []
  @Input() controlValue: PickerOption | null = null;
  @Input() clearable: boolean = false

  @Output() onSelect = new EventEmitter<any | null>()

  menuIsVisible = false

  open() {
    this.menuIsVisible = true
  }

  close() {
    this.menuIsVisible = false
  }

  toggle() {
    this.menuIsVisible = !this.menuIsVisible
  }

  onOptionSelected(event: MouseEvent, value: PickerOption | null) {
    event.stopImmediatePropagation();
    this.changeValue(value);
  }

  private changeValue(value: PickerOption | null) {
    this.controlValue = value;
    this.onSelect.emit(this.controlValue ? this.controlValue.value : null);
    this.menuIsVisible = false;
  }

  clear() {
    this.changeValue(null)
  }
}
