export type FilterConditionOperator = "=" | "<" | "<=" | ">" | ">=" | 'isnull';
export type FilterGroupOperator = "and" | "or";

export type FilterConditionEquals = { "=": { [key: string]: any } };
export type FilterConditionLessThan = { "<": { [key: string]: any } };
export type FilterConditionLessThanOrEqual = { "<=": { [key: string]: any } };
export type FilterConditionGreaterThan = { ">": { [key: string]: any } };
export type FilterConditionGreaterThanOrEqual = { ">=": { [key: string]: any } };
export type FilterConditionIsNull = { 'isnull': { [key: string]: any } };

export type FilterCondition =
    FilterConditionEquals |
    FilterConditionLessThan |
    FilterConditionLessThanOrEqual |
    FilterConditionGreaterThan |
    FilterConditionGreaterThanOrEqual |
    FilterConditionIsNull;

export type FilterGroupAnd = { "and": FilterLogicalGroup[] };
export type FilterGroupOr = { "or": FilterLogicalGroup[] };
export type FilterGroup = FilterGroupAnd | FilterGroupOr;

export type FilterLogicalGroup =
    FilterCondition |
    FilterGroup;

export class FilterBuilder {
    filter: FilterGroup;

    constructor(filter?: FilterLogicalGroup) {
        filter = !filter ? { "and": [] } : JSON.parse(JSON.stringify(filter)) as FilterLogicalGroup

        if (this.isFilterCondition(filter))
            filter = { "and": [filter] }

        this.filter = filter
    }

    insert(criteria: FilterLogicalGroup) {
        const root = <FilterGroupOperator>Object.keys(this.filter)[0];
        if (root === 'and') {
            (this.filter as FilterGroupAnd).and.push(criteria);
        } else if (root === 'or') {
            (this.filter as FilterGroupOr).or.push(criteria);
        } else {
            throw new Error('Operador de grupo inválido.');
        }
    }

    insertMany(criteria: FilterLogicalGroup[]) {
        criteria.forEach(c => this.insert(c))
    }


    encode(): string {
        const json = JSON.stringify(this.filter)
        return btoa(json)
    }

    private isFilterCondition(obj: any): obj is FilterCondition {
        return (
            obj.hasOwnProperty('=') ||
            obj.hasOwnProperty('<') ||
            obj.hasOwnProperty('<=') ||
            obj.hasOwnProperty('>') ||
            obj.hasOwnProperty('>=') ||
            obj.hasOwnProperty('isnull')
        );
    }
}