import { Component, Input } from '@angular/core';
import { IconDefinition } from '@fortawesome/fontawesome-common-types';
import { faAdd, faBars, faCaretDown, faCaretLeft, faCaretRight, faCaretUp, faCircleUser, faClose, faFileExport, faHome, faListCheck, faPencil, faRightFromBracket, faSearch, faStar, faTag, faTrash, faWrench, faXmark } from '@fortawesome/free-solid-svg-icons';

const iconMap: { [key: string]: IconDefinition } = {
  'home': faHome,
  'menu': faBars,
  'quotes': faTag,
  'registrations': faListCheck,
  'settings': faWrench,
  'user-photo': faCircleUser,
  'logout': faRightFromBracket,
  'add': faAdd,
  'edit': faPencil,
  'delete': faTrash,
  'search': faSearch,
  'previous': faCaretLeft,
  'next': faCaretRight,
  "close": faClose,
  "export": faFileExport,

  "up": faCaretUp,
  "down": faCaretDown,
  "left": faCaretLeft,
  "right": faCaretRight,
  "star": faStar
}

@Component({
  selector: 'quick-icon',
  template: '<fa-icon [icon]="getIcon()"></fa-icon>'
})
export class IconComponent {
  @Input() iconName?: string;

  getIcon() {
    return this.iconName ? iconMap[this.iconName] : faXmark;
  }
}
