import { Component, Input } from '@angular/core';
import { Image } from '../../shared/models/image.models';

@Component({
  selector: 'quick-shadowbox',
  templateUrl: './shadowbox.component.html',
  styleUrl: './shadowbox.component.scss'
})
export class ShadowboxComponent {
  @Input() images: Partial<Image>[] = []
  @Input() current: number = 0
  visible: boolean = false;

  show() {
    this.visible = true
  }

  dismiss(){
    this.visible = false
  }
}
