import { Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { Image } from '../../shared/models/image.models';



@Component({
  selector: 'quick-photo-galery',
  templateUrl: './photo-galery.component.html',
  styleUrl: './photo-galery.component.scss'
})
export class PhotoGaleryComponent {
  @Input() disabled = false
  @ViewChild('fileInput') fileInput?: ElementRef<HTMLInputElement>;

  @Output() onChange = new EventEmitter<Partial<Image>[]>();
  @Output() onRemove = new EventEmitter<Partial<Image>>();

  @Input() images: Partial<Image>[] = [];
  preview: number = 0;

  onFileSelected(event: any): void {
    const files: FileList = event.target.files;
    this.addImages(files);
  }

  onDragOver(event: DragEvent): void {
    event.preventDefault();
  }

  onDrop(event: DragEvent): void {
    event.preventDefault();
    const files: FileList = event.dataTransfer?.files || new FileList();
    this.addImages(files);
  }

  addImages(files: FileList): void {
    const loadImage = (file: File): Promise<Partial<Image>> => {
      return new Promise(resolve => {
        const reader = new FileReader();
        reader.onload = (e: any) => {
          resolve({ url: e.target.result, file });
        };
        reader.readAsDataURL(file);
      })
    }

    const tasks: Promise<Partial<Image>>[] = []
    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      tasks.push(loadImage(file))
    }

    Promise.all(tasks).then(images => {
      this.images.push(...images)
      this.onChange.emit(this.images)
    });
  }

  removeImage(image: Partial<Image>): void {
    const index = this.images.indexOf(image);
    if (index !== -1) {
      this.images.splice(index, 1);
      this.onRemove.emit(image)
      this.onChange.emit(this.images)
    }
  }

  openFilePicker() {
    this.fileInput?.nativeElement.click();
  }

}
