import { Component, Input, OnInit } from '@angular/core';
import { AbstractControl } from '@angular/forms';

@Component({
  selector: 'quick-form-autocomplete',
  templateUrl: './form-autocomplete.component.html',
  styleUrl: './form-autocomplete.component.scss'
})
export class FormAutocompleteComponent implements OnInit {
  @Input() options: string[] = []
  @Input() target?: HTMLInputElement
  @Input() control?: AbstractControl;

  private filterText = ""
  isVisible = false

  ngOnInit(): void {
    if (this.target) {
      this.target.autocomplete = "off"
      this.target.addEventListener('focus', this.onTargetFocus.bind(this))
      this.target.addEventListener('blur', this.onTargetBlur.bind(this))
      this.target.addEventListener('input', this.onTargetInput.bind(this))
    }
  }

  onTargetFocus(ev: Event) {
    this.isVisible = true;
    if (this.target)
      this.filterText = this.target.value
  }

  onTargetBlur(ev: Event) {
    setTimeout(() => {
      this.isVisible = false
    }, 150);
  }

  onTargetInput(ev: Event) {
    if (this.target)
      this.filterText = this.target.value
  }

  select(value: string) {
    if (this.control) {
      this.control.setValue(value)
    } else if (this.target) {
      this.target.value = value
    }
  }

  get filteredOptions() {
    return this.options.filter(o => o.toLowerCase().includes(this.filterText.toLowerCase()))
  }
}
